/* Grid
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.container,
.container-lg {
  --bs-gutter-x: 1rem;
  width: 100%;
  padding-right: var(--bs-gutter-x);
  padding-left: var(--bs-gutter-x);
  margin-right: auto;
  margin-left: auto; }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: calc(-0.5 * var(--bs-gutter-x));
  margin-left: calc(-0.5 * var(--bs-gutter-x)); }

.row > * {
  box-sizing: border-box;
  flex-shrink: 0;
  width: 100%;
  max-width: 100%;
  padding-right: calc(var(--bs-gutter-x) * 0.5);
  padding-left: calc(var(--bs-gutter-x) * 0.5); }

.col {
  flex: 1 0 0%; }

/* Base Styles
–––––––––––––––––––––––––––––––––––––––––––––––––– */
/* NOTE
html is set to 62.5% so that all the REM measurements throughout Skeleton
are based on 10px sizing. So basically 1.5rem = 15px :) */
html {
  font-size: 15px; }

body {
  font-size: 1rem;
  /* currently ems cause chrome bug misinterpreting rems on body element */
  line-height: 1.6;
  font-weight: 400;
  font-family: "Muli", sans-serif;
  color: #333333;
  position: relative; }

/* Typography
–––––––––––––––––––––––––––––––––––––––––––––––––– */
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-weight: 900; }

h1 {
  font-size: 2rem;
  line-height: 1.1; }

h2 {
  font-size: 1.6rem;
  line-height: 1.25; }

h3 {
  font-size: 1.4rem;
  line-height: 1.3; }

h4 {
  font-size: 1.5rem;
  line-height: 1.35; }

h5 {
  font-size: 1.4rem;
  line-height: 1.5; }

h6 {
  font-size: 1.3rem;
  line-height: 1.6; }

p {
  margin-top: 0; }

/* Links
–––––––––––––––––––––––––––––––––––––––––––––––––– */
a {
  color: #0c4e82;
  text-decoration: none; }

a:hover {
  color: #0b4674; }

/* Buttons
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.btn {
  display: inline-block;
  height: 2.63158rem;
  padding: 0 1.42105rem;
  color: #fff;
  text-align: center;
  font-size: 0.94737rem;
  font-weight: 400;
  line-height: 2.63158rem;
  text-decoration: none;
  white-space: nowrap;
  background-color: #0c4e82;
  border-radius: 30px;
  border: 1px solid #0c4e82;
  cursor: pointer;
  vertical-align: middle; }
  .btn .icon {
    width: 1em;
    height: 1em;
    color: #fff;
    position: relative;
    top: 0.15789rem;
    margin-left: .5rem; }

.btn:hover,
.btn:focus {
  color: #fff;
  background-color: #0b4674;
  border-color: #0b4674;
  outline: 0; }

.btn.btn-info {
  background-color: #00c4e1;
  border-color: #00c4e1;
  font-weight: 900;
  font-size: 1rem;
  padding: 0 1.84211rem;
  height: 2.78947rem;
  line-height: 2.78947rem; }

.btn.btn-info:hover,
.btn.btn-info:focus {
  background-color: #00b7d2;
  border-color: #00b7d2; }

/* Lists
–––––––––––––––––––––––––––––––––––––––––––––––––– */
ul {
  list-style: circle inside; }

ol {
  list-style: decimal inside; }

ol, ul {
  padding-left: 0;
  margin-top: 0; }

ul ul,
ul ol,
ol ol,
ol ul {
  margin: 1.5rem 0 1.5rem 3rem;
  font-size: 90%; }

li {
  margin-bottom: 1rem; }

/* Code
–––––––––––––––––––––––––––––––––––––––––––––––––– */
code {
  padding: .2rem .5rem;
  margin: 0 .2rem;
  font-size: 90%;
  white-space: nowrap;
  background: #F1F1F1;
  border: 1px solid #E1E1E1;
  border-radius: 4px; }

pre > code {
  display: block;
  padding: 1rem 1.5rem;
  white-space: pre; }

/* Tables
–––––––––––––––––––––––––––––––––––––––––––––––––– */
th,
td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #E1E1E1; }

th:first-child,
td:first-child {
  padding-left: 0; }

th:last-child,
td:last-child {
  padding-right: 0; }

/* Spacing
–––––––––––––––––––––––––––––––––––––––––––––––––– */
input,
textarea,
select,
fieldset {
  margin-bottom: 1.5rem; }

pre,
blockquote,
dl,
figure,
table,
p,
ul,
ol,
form {
  margin-bottom: 1.5rem; }

/* Misc
–––––––––––––––––––––––––––––––––––––––––––––––––– */
hr {
  margin-top: 3rem;
  margin-bottom: 3.5rem;
  border-width: 0;
  border-top: 1px solid #E1E1E1; }

img {
  max-width: 100%;
  height: auto; }

.hidden {
  display: none; }

/* Colors
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.text-cyan {
  color: #00c4e1; }

.text-blue {
  color: #1e344d; }

/* Paddings
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.py-70 {
  padding-top: 35px;
  padding-bottom: 35px; }

.text-center {
  text-align: center; }

/*Header*/
.site-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  padding: 20px 0;
  text-align: center; }
  .site-header .row {
    align-items: center; }
  .site-header .logo img {
    width: 13.31579rem;
    height: auto; }

/* Main Banner */
.main-banner {
  background: #0c4e82 url("../img/main-banner-bg.jpg") 50%/cover no-repeat;
  color: #fff;
  min-height: 100vh;
  display: flex;
  align-items: center; }
  .main-banner .h1 {
    text-transform: uppercase; }
    .main-banner .h1 span {
      color: #00c4e1; }
  .main-banner .badge {
    display: inline-block;
    margin-bottom: 1rem;
    font-size: 0.94737rem;
    border: 1px solid white;
    border-radius: 0.52632rem;
    padding: 0.31579rem 0.89474rem; }

.buy-ticket {
  display: inline-block;
  background-color: #00c4e1;
  padding: 0.63158rem;
  border-radius: 1.94737rem; }
  .buy-ticket .inner {
    display: flex;
    align-items: center; }
  .buy-ticket strong {
    margin: 0 20px;
    color: #fff; }

/* Content section */
.section-title {
  text-align: center;
  color: #1e344d;
  position: relative;
  margin-bottom: 3rem; }
  .section-title:after {
    content: '';
    display: inline-block;
    width: 4.21053rem;
    height: 0.26316rem;
    position: absolute;
    left: 50%;
    bottom: -1.31579rem;
    transform: translateX(-50%);
    background-color: #00c4e1; }

.register-now {
  display: inline-block;
  margin-left: auto;
  position: relative;
  margin-top: 2rem; }
  .register-now .inner {
    position: absolute;
    top: 50%;
    left: 45%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff; }
  .register-now .date {
    font-weight: 900;
    font-size: 7.10526rem;
    line-height: 1; }
  .register-now .month {
    font-size: 1.42105rem;
    line-height: 1;
    font-weight: 900;
    border-bottom: 1px solid #fff;
    padding-bottom: 1rem;
    margin-bottom: .7rem; }
  .register-now a {
    color: #00c4e1;
    font-size: 1.36842rem;
    line-height: 1; }

