/* Larger than tablet */
@media (min-width: 768px) {
  .col-md-6 {
    flex: 0 0 auto;
    width: 50%; }
  .ta-md-right {
    text-align: right; }
  .py-70 {
    padding-top: 70px;
    padding-bottom: 70px; }
  h1 {
    font-size: 4.21875vw; }
  h2 {
    font-size: 2.15789rem; }
  h3 {
    font-size: 1.42105rem; }
  h4 {
    font-size: 3.0rem; }
  h5 {
    font-size: 2.4rem; }
  h6 {
