/* Larger than desktop */
@media (min-width: 992px) {
  html {
    font-size: 17px; } }

/* Larger than Desktop HD */
@media (min-width: 1280px) {
  html {
    font-size: 18px; } }

@media (min-width: 1400px) {
  html {
    font-size: 19px; }
  .container,
  .container-lg {
    --bs-gutter-x: 1.5rem; }
  .container {
    max-width: 1307px; }
  .container-lg {
    max-width: 80%; } }

@media (max-width: 767px) {
  .site-header .col-logo {
    margin-bottom: 1rem; }
  .sm-hide {
    display: none; }
  .secrets {
    text-align: center; }
    .secrets .col-md-6 > div {
      margin-left: auto;
      margin-right: auto; } }

@media (max-width: 479px) {
  .buy-ticket {
    border-radius: 1rem; }
    .buy-ticket .inner {
      flex-direction: column; }
      .buy-ticket .inner strong {
        margin-bottom: .5rem; } }
